import React from 'react'
import Helmet from 'react-helmet'
import { graphql } from "gatsby"
import Layout from '../components/layout'
import Posts from '../components/Posts'
import Back from "../components/back-button"
import SEO from "../components/seo"

const WorkPage = ({ siteTitle, siteDescription, data }) => (
    <div>
        <Helmet>
            <title>{siteTitle}</title>
            <meta name="description" content={siteDescription} />
        </Helmet>
        <div id="main">
            <Back  dest="/" />
            <div className="major">
                <h2>Work and Projects</h2>
            </div>
            <Posts posts={data.WorkMD.edges} />
        </div>
        <SEO title="Greenberg's Work" keywords={[`geology`, `penn state`, `geography`, `university of california`, `graduate`, `greenberg`, `evan`]} />
    </div>
)

export default WorkPage

export const query = graphql`
  query {
    WorkMD: allMarkdownRemark(
        filter: { frontmatter: { type: { eq: "work" } } }
        sort: { order: DESC, fields: [frontmatter___date]},
    ) {
        edges {
            node {
                html
                frontmatter {
                    title
                    date
                    image
                }
                fields {
                    image {
                        childImageSharp {
                            fluid(maxHeight: 500) {
                                ...GatsbyImageSharpFluid
                            }
                        }
                    }
                }
            }
        }
    }
}
`;
