import React from 'react';
import Post from './Post';

const split = items => {
  let article = [];

  items.map((item, i) => {
      article.push(item);
  });

  return (
    <div>
      {article}
    </div>
  );
};

const preparePosts = posts => {
  const nodes = posts.map(post => {
      return <Post post={post} />;
  });

  return split(nodes);
};

export default ({ posts }) => (
  <div id="work" className="posts">
    {preparePosts(posts)}
  </div>
);
